import Vue from 'vue';
import Element from 'element-ui';
import { Message } from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(Element);

new Vue({
    el: '#controller',
    mounted: function init(){
        this.optionValue = this.options[0].value;
    },
    data:{
        optionValue: null,
        beginEndTime: null,
        options:[{
            value: '采集点0',
            label: '神宇北斗'
        },{
            value: '采集点1',
            label: '其他1'
        },{
            value: '采集点2',
            label: '其他2'
        }],
        pickerOptions2: {
            clearable: false,
            editable: false,
            shortcuts: [{
                text: '最近一周',
                onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
                    picker.$emit('pick', [start, end]);
                }
            }, {
                text: '最近一个月',
                onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
                    picker.$emit('pick', [start, end]);
                }
            }, {
                text: '最近三个月',
                onClick(picker) {
                    const end = new Date();
                    const start = new Date();
                    start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
                    picker.$emit('pick', [start, end]);
                }
            }]
        }
    },
    methods:{
        search: function (event) {
            this.$alert('搜索功能未开放', '注意', {
                confirmButtonText: '确定',
                callback: action => {
                    this.beginEndTime = null;
                }
            });
            return false;
            let begin = new Date(Date.parse(this.beginEndTime[0]));
            let end = new Date(Date.parse(this.beginEndTime[1]));
            window.open(`detail/${begin.valueOf()}/${end.valueOf()}`);
        }
    },
});
